.Home {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: #000000;
}
.Home-presetation {
  position: relative;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #000000;
}
.Home-title {
  position: absolute;
  left: 25px;
  width: 100%;
  color: white;
  font-size: 4rem;
}

.Home-logo {
  height: 100%;
  background: red;
  -webkit-clip-path: polygon(24% 0, 100% 0, 100% 50%, 100% 100%, 0% 100%);
          clip-path: polygon(24% 0, 100% 0, 100% 50%, 100% 100%, 0% 100%);
}



